.edit-dish {
  display: flex;
}
.edit-dish .dishes-overview {
  width: 17%;
}
.edit-dish .dishes {
  width: 75%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.edit-dish .dishes .dish {
  width: 30%;
  margin-bottom: 20px;
  margin-right: 5%;
  border-radius: 10px;
  -webkit-transition: margin 0.3s;
  transition: margin 0.3s;
  overflow: hidden;
  box-shadow: 2px 2px 3px #ccc, -2px -2px 3px #eee;
}
.edit-dish .dishes .dish:nth-child(3n) {
  margin-right: 0;
}
.edit-dish .dishes .dish:hover {
  margin-top: -3px;
}
.edit-dish .dishes .dish .ant-carousel .slick-slide {
  text-align: center;
  height: 200px;
  line-height: 200px;
  background: #364d79;
  overflow: hidden;
}
.edit-dish .dishes .dish .ant-carousel .slick-slide img {
  width: 100%;
  height: 100%;
}
.edit-dish .dishes .dish .ant-carousel {
  min-height: 200px;
  background: #eee;
}
.edit-dish .dishes .dish .text-area {
  box-sizing: border-box;
  padding: 10px 20px;
}
.edit-dish .dishes .dish .text-area .dish-name {
  color: #364d79;
  font-size: 23px;
  margin-bottom: 10px;
}
.edit-dish .dishes .dish .text-area .fields {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.edit-dish .dishes .dish .text-area .fields > div {
  width: 50%;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}
.edit-dish .dishes .dish .text-area .fields > div > span:nth-child(1) {
  display: inline-block;
  min-width: 50px;
  background: skyblue;
  height: 25px;
  line-height: 25px;
  text-align: center;
  margin-right: 8px;
  border-radius: 3px;
}
.edit-dish .dishes .dish .text-area .fields > div > span:nth-child(2) {
  color: #999;
  font-size: 14px;
}
.edit-dish .dishes .dish .text-area .desc {
  font-size: 14px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.edit-dish .dishes .dish .icons {
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
  font-size: 25px;
  color: #69BFFD;
}
.edit-modal .images {
  display: flex;
  flex-wrap: wrap;
  min-height: 80px;
}
.edit-modal .images .image-wrapper {
  position: relative;
  max-width: 80px;
  max-height: 80px;
  border-radius: 3px;
  overflow: hidden;
}
.edit-modal .images .image-wrapper:hover .delete-img {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.edit-modal .images .image-wrapper .delete-img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 16px;
  background: rgba(0, 0, 0, 0.3);
}
.edit-modal .images .image-wrapper img {
  width: 100%;
  height: 100%;
}
.edit-modal .input-wrapper {
  display: flex;
  margin-bottom: 8px;
}
.edit-modal .input-wrapper:nth-child(2) {
  margin-top: 10px;
}
.edit-modal .input-wrapper > span {
  min-width: 50px;
  height: 25px;
  line-height: 25px;
  margin-right: 20px;
  text-align: center;
}
.edit-modal .input-wrapper > input,
.edit-modal .input-wrapper > textarea {
  flex: 1 1;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
}
.edit-modal .input-wrapper > input {
  height: 30px;
  padding-left: 10px;
}
