.order-page .order-btns {
  height: 30px;
  margin-bottom: 15px;
}
.order-page .orders .order-item {
  margin-bottom: 20px;
  display: flex;
  border-radius: 8px;
  flex-wrap: wrap;
  overflow: hidden;
  background: #fff;
  padding: 8px 10px;
}
.order-page .orders .order-item .order-dish {
  width: 24%;
  height: 150px;
  margin-right: 10px;
  border: 1px solid #fff;
  display: flex;
  background: #e6f7ff;
}
.order-page .orders .order-item .order-dish:nth-child(4n) {
  margin-right: 0;
}
.order-page .orders .order-item .order-dish .order-dish-img-wrapper {
  margin-right: 10px;
}
.order-page .orders .order-item .order-dish .order-dish-img-wrapper > img {
  height: 150px;
  width: 150px;
}
.order-page .orders .order-item .order-dish .dish-overview > div {
  height: 30px;
  line-height: 30px;
}
.order-page .orders .order-item .order-dish .dish-overview > div > span:nth-child(1) {
  color: #aaa;
}
.order-page .orders .order-item .order-overview {
  height: 150px;
  padding: 5px 10px;
}
.order-page .orders .order-item .order-overview > div {
  height: 30px;
  line-height: 30px;
}
